import _forEach from 'lodash.foreach'
import _clonedeep from 'lodash.clonedeep'

export default {
  props: {
    /**
     * @description dialog
     */
    formOptions: {
      type: Object,
      default: null
    },
    /**
     * @description dialog
     */
    addTitle: {
      type: String,
      default: ''
    },
    /**
     * @description dialog
     */
    editTitle: {
      type: String,
      default: ''
    },
    /**
     * @description dialog
     */
    addWidth: {
      type: String,
      default: ''
    },
    /**
     * @description dialog
     */
    editWidth: {
      type: String,
      default: ''
    },
    /**
 * @description dialog
 */
    lookTitle: {
      type: String,
      default: ''
    },
    /**
     * @description 
     */
    addTemplate: {
      type: Object,
      default: null
    },
    /**
     * @description 
     */
    editTemplate: {
      type: Object,
      default: null
    },
    /**
    * @description 
    */
    lookTemplate: {
      type: Object,
      default: null
    },
    /**
     * @description 
     */
    addRules: {
      type: Object,
      default: null
    },
    /**
     * @description 
     */
    editRules: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      /**
       * @description dialog
       */
      isDialogShow: false,
      /**
       * @description 
       */
      formData: {},
      /**
       * @description 
       */
      formMode: 'edit',
      watchEditOpen: 0,
      /**
       * @description editTemplate
       */
      editDataStorage: {},
      /**
       * @description 
       */
      addTemplateStorage: {},
      /**
       * @description 
       */
      editTemplateStorage: {},
      /**
       * @description editTemplate
       */
      lookDataStorage: {},
      /**
      * @description 
      */
      lookTemplateStorage: {}
    }
  },
  methods: {
    /**
     * @description 
     */
    handleDialogSave() {
      this.$refs.form.validate((valid) => {
        if (!valid) {
          return false
        }
        let rowData = {}
        if (this.formMode === 'edit') {
          rowData = _clonedeep(this.editDataStorage)
          _forEach(this.formData, (value, key) => {
            this._set(rowData, key, value)
          })
          // 
          let obj = _clonedeep(this.editTemplateStorage)
          for (let key of Object.keys(obj)) {
            if (obj[key].component.show === undefined) {
              obj[key].component.show = true
            }
            if (!obj[key].component.show) {
              delete rowData[key]
            }
          }
          this.$emit('row-edit', {
            index: this.editIndex,
            row: rowData
          }, (param = null) => {
            if (param === false) {
              this.handleCloseDialog()
              return
            }
            this.handleDialogSaveDone({
              ...rowData,
              ...param
            })
          })
        } else if (this.formMode === 'add') {
          _forEach(this.formData, (value, key) => {
            this._set(rowData, key, value)
          })
          // 
          let obj = _clonedeep(this.addTemplateStorage)
          for (let key of Object.keys(obj)) {
            if (obj[key].component.show === undefined) {
              obj[key].component.show = true
            }
            if (!obj[key].component.show) {
              delete rowData[key]
            }
          }
          this.$emit('row-add', rowData, (param = null) => {
            if (param === false) {
              this.handleCloseDialog()
              return
            }
            this.handleDialogSaveDone({
              ...rowData,
              ...param
            })
          })

        } else {
          this.handleCloseDialog()
        }
      })
    },
    /**
     * @description 
     */
    handleDialogCancel(done) {
      this.$emit('dialog-cancel', done)
    },
    /**
     * @description 
     */
    handleDialogSaveDone(rowData) {
      if (this.formMode === 'edit') {
        this.handleUpdateRow(this.editIndex, rowData)
        this.editDataStorage = {}
      } else if (this.formMode === 'add') {
        this.handleAddRow(rowData)
      }
      this.handleCloseDialog()
    },
    /**
     * @description 
     */
    handleCloseDialog() {
      this.isDialogShow = false
    }
  },
  watch: {
    addTemplate: {
      handler(newV, oldV) {
        if (this.formMode === "add") {
          let data = {};
          // 
          let formDataStorge = _clonedeep(this.formData);
          this.addTemplateStorage = _clonedeep(newV)
          _forEach(formDataStorge, (value, key) => {
            this.formData[key] = formDataStorge[key]
          })
        }

      },
      deep: true,
    },
    editTemplate: {
      handler(newV, oldV) {
        if (this.formMode === "edit") {
          this.editDataStorage = _clonedeep(this.d2CrudData[this.editIndex])
          let formDataStorge = _clonedeep(this.formData);
          this.editTemplateStorage = _clonedeep(newV)
          _forEach(formDataStorge, (value, key) => {
            this.formData[key] = formDataStorge[key]
          })
        }
      },
      deep: true,
    },
    lookTemplate: {
      handler(newV, oldV) {
        if (this.formMode === "look") {
          this.handleLook(this.lookIndex, this.d2CrudData[this.lookIndex], newV)
        }
      },
      deep: true
    }

  },
}
